import './index.css'
import React from 'react'
import AIOperationsComplete from './components/ai-operations-complete'
import Login from './components/Login'

function App() {
  const [authed, setAuthed] = React.useState(false)
  return authed ? (
    <AIOperationsComplete />
  ) : (
    <Login onLogin={() => setAuthed(true)} />
  )
}

export default App
