import React from 'react';
import { Mail, Lock, ArrowRight } from 'lucide-react';

type Props = { onLogin: () => void };

const Login: React.FC<Props> = ({ onLogin }) => {
  return (
  <div
    className="min-h-screen relative bg-slate-900 bg-cover bg-no-repeat md:bg-right bg-center"
    style={{ backgroundImage: "url('/login-bg.png')" }}
  >
  <div className="relative z-10 max-w-[420px] mx-auto px-6 pt-56 sm:pt-64 md:pt-72 lg:pt-80 pb-20 md:translate-x-2 lg:translate-x-4 translate-y-2 sm:translate-y-3 md:translate-y-4">

        <div className="rounded-2xl shadow-2xl border border-white/10 overflow-hidden bg-white/5 backdrop-blur-md">
          <div className="p-6">
            {/* Brand inside the card */}
            <div className="text-center mb-5">
              <h1 className="text-2xl font-black text-white tracking-tight">Porsche Center Lebanon</h1>
              <p className="text-sm text-white/70">Smart communication hub</p>
            </div>
            <h2 className="text-lg font-black text-white">Sign in</h2>
            <p className="text-sm text-white/80 mt-1">Use the demo credentials below or simply continue</p>

            <div className="mt-6 space-y-4">
              <div>
                <label className="text-xs font-semibold text-white/80">Email</label>
                <div className="mt-1 relative">
                  <Mail className="w-4 h-4 text-white/70 absolute left-3 top-1/2 -translate-y-1/2" />
                  <input
                    type="email"
                    placeholder="you@porsche.com"
                    className="w-full pl-10 pr-3 py-3 border rounded-xl focus:ring-2 focus:ring-red-500/40 focus:border-red-500 bg-white/10 border-white/20 text-white placeholder-white/60"
                    defaultValue="demo@porsche.com"
                    aria-label="Email"
                  />
                </div>
              </div>
              <div>
                <label className="text-xs font-semibold text-white/80">Password</label>
                <div className="mt-1 relative">
                  <Lock className="w-4 h-4 text-white/70 absolute left-3 top-1/2 -translate-y-1/2" />
                  <input
                    type="password"
                    placeholder="••••••••"
                    className="w-full pl-10 pr-3 py-3 border rounded-xl focus:ring-2 focus:ring-red-500/40 focus:border-red-500 bg-white/10 border-white/20 text-white placeholder-white/60"
                    defaultValue="porsche123"
                    aria-label="Password"
                  />
                </div>
              </div>

              <button
                onClick={onLogin}
                className="w-full group mt-2 inline-flex items-center justify-center px-4 py-3 rounded-xl bg-[#B12B2B] text-white font-semibold shadow-lg hover:shadow-xl transition-all"
                aria-label="Log in"
              >
                Log in
                <ArrowRight className="w-5 h-5 ml-2 translate-x-0 group-hover:translate-x-0.5 transition-transform" />
              </button>

              <div className="pt-4 border-t border-white/10 text-[11px] text-white/70">
                This demo login simply reveals the mockup dashboard. No credentials are sent or stored.
              </div>
            </div>
          </div>
        </div>

        <div className="mt-8 text-center text-xs text-white/70">© {new Date().getFullYear()} Porsche Center Lebanon</div>
      </div>
    </div>
  );
};

export default Login;
