// Types for our data structures
interface EmailInquiry {
  id: string;
  from: string;
  subject: string;
  content: string;
  timestamp: Date;
  category: 'HR' | 'Sales' | 'After-Sales' | 'VIP';
  priority: 'urgent' | 'high' | 'medium' | 'low';
  status: 'unread' | 'read' | 'processing' | 'responded' | 'escalated';
  sentiment: 'positive' | 'neutral' | 'negative';
  leadPotential: 'high' | 'medium' | 'low';
  archived?: boolean;
  // Optional enriched profile fields
  customerSince?: string;
  segment?: 'VIP' | 'Premium' | 'Standard' | 'New' | string;
  lifetimeValue?: number;
  currentCar?: string;
  language?: string;
  slaTier?: 'urgent' | 'high' | 'medium' | 'low' | string;
  tags?: string[];
  // Premium optional fields
  aiConfidence?: number; // 0..1
  responseTime?: number; // minutes
  dealValue?: number; // in currency units
  nextBestAction?: string;
}

interface WhatsAppMessage {
  id: string;
  from: string;
  phone: string;
  content: string;
  timestamp: Date;
  category: 'Sales' | 'Workshop' | 'VIP';
  priority: 'urgent' | 'high' | 'medium' | 'low';
  status: 'unread' | 'read' | 'processing' | 'responded' | 'escalated';
  sentiment: 'positive' | 'neutral' | 'negative';
  leadPotential: 'high' | 'medium' | 'low';
  // Optional enriched profile fields
  customerSince?: string;
  segment?: 'VIP' | 'Premium' | 'Standard' | 'New' | string;
  lifetimeValue?: number;
  currentCar?: string;
  language?: string;
  slaTier?: 'urgent' | 'high' | 'medium' | 'low' | string;
  tags?: string[];
  // Premium optional fields
  aiConfidence?: number;
  responseTime?: number;
  dealValue?: number;
  nextBestAction?: string;
}

interface CustomerInsight {
  id: string;
  type: 'pattern' | 'trend' | 'opportunity' | 'risk' | 'prediction';
  title: string;
  description: string;
  impact: 'high' | 'medium' | 'low' | 'critical';
  category: 'sales' | 'service' | 'engagement' | 'revenue';
  timestamp: Date;
  confidence?: number;
  actionRequired?: boolean;
  estimatedValue?: number;
  affectedItems?: number;
  recommendation?: string;
  bookmarked?: boolean;
  resolved?: boolean;
}

