  // Helpers
  const formatBytes = (bytes: number) => {
    if (bytes === 0) return '0 B';
    const k = 1024;
    const sizes = ['B', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return `${(bytes / Math.pow(k, i)).toFixed(1)} ${sizes[i]}`;
  };

  const updateDraft = (id: string, next: string) => setChatDrafts(prev => ({ ...prev, [id]: next }));

  const insertAtCursor = (id: string, text: string) => {
    const current = chatDrafts[id] || '';
    const el = composerRef.current;
    if (!el) {
      updateDraft(id, current + text);
      return;
    }
    const start = el.selectionStart ?? current.length;
    const end = el.selectionEnd ?? current.length;
    const before = current.slice(0, start);
    const after = current.slice(end);
    const next = before + text + after;
    updateDraft(id, next);
    // restore caret
    setTimeout(() => {
      try {
        el.focus();
        const pos = start + text.length;
        el.setSelectionRange(pos, pos);
      } catch {}
    }, 0);
  };

  const wrapSelection = (id: string, left: string, right: string) => {
    const current = chatDrafts[id] || '';
    const el = composerRef.current;
    if (!el) return insertAtCursor(id, left + right);
    const start = el.selectionStart ?? current.length;
    const end = el.selectionEnd ?? current.length;
    const selected = current.slice(start, end);
    const next = current.slice(0, start) + left + (selected || 'text') + right + current.slice(end);
    updateDraft(id, next);
    setTimeout(() => {
      try {
        el.focus();
        const selEnd = start + left.length + (selected || 'text').length + right.length;
        el.setSelectionRange(selEnd, selEnd);
      } catch {}
    }, 0);
  };

  const applyFormat = (id: string, action: 'bold' | 'italic' | 'bullets' | 'numbers' | 'link' | 'quote') => {
    if (!id) return;
    const current = chatDrafts[id] || '';
    const el = composerRef.current;
    switch (action) {
      case 'bold':
        return wrapSelection(id, '*', '*'); // WhatsApp bold
      case 'italic':
        return wrapSelection(id, '_', '_');
      case 'link':
        return insertAtCursor(id, ' https://porsche.com ');
      case 'quote': {
        if (!el) return insertAtCursor(id, '> ');
        const start = el.selectionStart ?? 0;
        const end = el.selectionEnd ?? 0;
        const lines = current.split('\n');
        let pos = 0;
        for (let i = 0; i < lines.length; i++) {
          const line = lines[i];
          const lineStart = pos;
          const lineEnd = pos + line.length;
          if (lineEnd >= start && lineStart <= end) {
            lines[i] = `> ${line}`;
          }
          pos = lineEnd + 1;
        }
        return updateDraft(id, lines.join('\n'));
      }
      case 'bullets':
      case 'numbers': {
        if (!el) return insertAtCursor(id, action === 'bullets' ? '- ' : '1. ');
        const start = el.selectionStart ?? 0;
        const end = el.selectionEnd ?? 0;
        const lines = current.split('\n');
        let pos = 0;
        let count = 1;
        for (let i = 0; i < lines.length; i++) {
          const line = lines[i];
          const lineStart = pos;
          const lineEnd = pos + line.length;
          if (lineEnd >= start && lineStart <= end) {
            lines[i] = action === 'bullets' ? `- ${line}` : `${count}. ${line}`;
            count++;
          }
          pos = lineEnd + 1;
        }
        return updateDraft(id, lines.join('\n'));
      }
    }
  };

