import React, { useMemo, useRef, useState } from 'react';
import { X, Sparkles, Loader2, Check, Calendar, FileText, PhoneCall, Paperclip, Image as ImageIcon, Smile } from 'lucide-react';

import type { Tone } from '../types';

export interface EmailInquiry {
  id: string;
  from: string;
  subject: string;
  content: string;
  timestamp: Date;
  category: 'HR' | 'Sales' | 'After-Sales' | 'VIP';
  priority: 'urgent' | 'high' | 'medium' | 'low';
  status: 'unread' | 'read' | 'processing' | 'responded' | 'escalated';
  sentiment: 'positive' | 'neutral' | 'negative';
  leadPotential: 'high' | 'medium' | 'low';
  archived?: boolean;
  customerSince?: string;
  segment?: 'VIP' | 'Premium' | 'Standard' | 'New' | string;
  lifetimeValue?: number;
  currentCar?: string;
  language?: string;
  slaTier?: 'urgent' | 'high' | 'medium' | 'low' | string;
  tags?: string[];
  aiConfidence?: number;
  responseTime?: number;
  dealValue?: number;
  nextBestAction?: string;
}

type PrevInteraction = { id: string; subject: string; date: string; status: string };

interface Props {
  email: EmailInquiry;
  draft: string;
  onChangeDraft: (v: string) => void;
  suggestions: string[];
  onGenerateSuggestions: (tone: Tone) => void;
  onClose: () => void;
  onSend: (payload: { draft: string; cta?: string; attachments?: File[] }) => void;
  prevInteractions?: PrevInteraction[];
}

const StepPill: React.FC<{ idx: number; label: string; active: boolean; done?: boolean; onClick?: () => void }> = ({ idx, label, active, done, onClick }) => (
  <button
    type="button"
    onClick={onClick}
  className={`flex items-center gap-2 px-3 py-1.5 rounded-full text-xs font-semibold border focus:outline-none focus:ring-2 focus:ring-[#A43131]/30 ${
      active ? 'bg-[#A43131] text-white border-[#A43131]' : done ? 'bg-[#C29049]/15 text-[#402306] border-[#C29049]/40' : 'bg-white text-slate-700 border-slate-200 hover:bg-slate-50'
    }`}
  >
    <span className={`w-5 h-5 rounded-full flex items-center justify-center text-[11px] ${active ? 'bg-white/20' : 'bg-slate-100 text-slate-700'}`}>{idx}</span>
    {label}
  </button>
);

const EmailResponseWorkspace: React.FC<Props> = ({ email, draft, onChangeDraft, suggestions, onGenerateSuggestions, onClose, onSend, prevInteractions = [] }) => {
  const [tone, setTone] = useState<Tone>('Neutral');
  const [step, setStep] = useState<'draft' | 'cta'>('draft');
  const [isGenLoading, setIsGenLoading] = useState(false);
  const [selectedCTA, setSelectedCTA] = useState<string | null>(null);
  const [note, setNote] = useState('');
  const [attachments, setAttachments] = useState<File[]>([]);
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const [toast, setToast] = useState<string | null>(null);
  const showToast = (msg: string) => { setToast(msg); window.setTimeout(() => setToast(null), 2200); };

  const sendEnabled = draft.trim().length > 0;

  const ctas = useMemo(() => {
    if (email.category === 'Sales' || email.category === 'VIP') {
      return ['Schedule Test Drive', 'Share Finance Options', 'Send Personalized Offer', 'Arrange Manufaktur Appointment'];
    }
    if (email.category === 'After-Sales') {
      const base = ['Book Service Slot', 'Share Warranty Info'];
      const extras: string[] = [];
      if (email.sentiment === 'negative' || email.priority === 'urgent' || email.slaTier === 'urgent') {
        extras.push('Check Workshop Status', 'Escalate Case', 'Offer Loaner Vehicle');
      } else {
        extras.push('Loaner Vehicle Request');
      }
      return [...base, ...extras];
    }
    return ['Acknowledge & Route', 'Share Careers Link'];
  }, [email.category, email.priority, email.sentiment, email.slaTier]);

  const contextualSuggestions = useMemo(() => {
    if (email.category === 'After-Sales') {
      if (email.sentiment === 'negative') {
        return [
          "Thank you for your patience. I understand the delay is frustrating. I'll check your vehicle's current workshop status and update you shortly.",
          'We can prioritize your case and, if needed, arrange a loaner vehicle while your car is in the workshop. Would that help?',
          'I can escalate this to our service manager and confirm compensation eligibility based on workshop timelines. May I proceed?',
        ];
      }
      return [
        'Thanks for reaching out. I can check your service timing and share the earliest available slot for next week.',
        'Would you like us to arrange pick-up and drop-off for your vehicle to save you time?',
      ];
    }
    if (email.category === 'Sales' || email.category === 'VIP') {
      return [
        'Great choice. I can share a 48- vs 60-month finance comparison with 20% down and schedule a test drive—do you prefer weekdays or weekends?',
        'I can prepare a personalized offer based on your preferred configuration and delivery timeline. Any specific options you want to include?',
      ];
    }
    return [
      'Thank you for your interest. Please share your CV and availability. I will route your application to our HR team and confirm next steps.',
    ];
  }, [email.category, email.sentiment]);

  const doGenerate = async () => {
    setIsGenLoading(true);
    await new Promise(r => setTimeout(r, 400));
    onGenerateSuggestions(tone);
    setIsGenLoading(false);
  showToast('Suggestions updated');
  };

  const triggerFile = () => fileInputRef.current?.click();
  const onFilesSelected = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(e.target.files || []);
    if (files.length) setAttachments(prev => [...prev, ...files]);
    if (fileInputRef.current) fileInputRef.current.value = '';
  };
  const removeAttachment = (idx: number) => setAttachments(prev => prev.filter((_, i) => i !== idx));

  const formatMoney = (n?: number) => {
    if (!n && n !== 0) return '—';
    return n >= 1_000_000 ? `$${(n / 1_000_000).toFixed(1)}M` : `$${Math.round(n / 1000)}K`;
  };

  const doSend = () => {
    if (!sendEnabled) return;
    onSend({ draft, cta: selectedCTA || undefined, attachments });
    showToast('Email sent');
  };

  return (
    <div className="fixed inset-0 z-[80] bg-black/50 backdrop-blur-sm flex items-center justify-center">
      <div className="relative w-[96vw] max-w-[1400px] h-[88vh] bg-white rounded-2xl shadow-2xl border border-slate-200 overflow-hidden">
        {/* Header */}
        <div className="flex items-center justify-between px-6 py-4 border-b border-slate-200 bg-gradient-to-r from-red-50 to-white">
          <div className="min-w-0">
            <div className="text-xs text-slate-600">Replying to</div>
            <div className="flex items-center gap-2">
              <div className="font-black text-slate-900 truncate max-w-[40ch]">{email.from}</div>
              {(email.segment === 'VIP' || email.category === 'VIP') && (
                <span className="px-1.5 py-0.5 rounded-full text-[10px] bg-[#C29049]/15 text-[#402306] border border-[#C29049]/40 font-bold">VIP</span>
              )}
              <span className="px-2 py-0.5 rounded-full text-[10px] bg-[#A43131]/10 text-[#A43131] border border-[#A43131]/30">{email.category}</span>
              <span className="text-[11px] text-slate-500">• {email.timestamp.toLocaleString()}</span>
            </div>
            <div className="text-[13px] text-slate-600 truncate max-w-[60ch]">Subject: {email.subject}</div>
          </div>
          <div className="flex items-center gap-3">
            <button onClick={onClose} className="px-3 py-1.5 rounded-lg border bg-white text-slate-700 hover:bg-slate-50 text-sm"><X className="w-4 h-4 inline mr-1" /> Close</button>
            <button
              disabled={!sendEnabled}
              onClick={doSend}
              className={`px-4 py-2 rounded-xl text-white font-semibold shadow ${sendEnabled ? 'bg-[#A43131] hover:bg-[#902828]' : 'bg-slate-400/50 cursor-not-allowed'}`}
            >Send</button>
          </div>
        </div>

        {/* 3-Pane Body */}
        <div className="grid grid-cols-12 h-[calc(88vh-64px)]">
          {/* Left: Inquiry context & History */}
          <aside className="col-span-3 border-r border-slate-200 bg-slate-50/50 overflow-y-auto">
            <div className="p-4">
              <div className="text-xs font-semibold text-slate-600 mb-2">Original Inquiry</div>
              <div className="bg-white border border-slate-200 rounded-xl p-3 shadow-sm">
                <div className="text-[13px] text-slate-800 whitespace-pre-wrap leading-relaxed">{email.content}</div>
              </div>
              {/* Tags */}
              {email.tags && email.tags.length > 0 && (
                <div className="mt-3 flex flex-wrap gap-2">
                  {email.tags.map(t => (
                    <span key={t} className="px-2 py-1 rounded-full text-[11px] bg-slate-100 border border-slate-200 text-slate-700">{t}</span>
                  ))}
                </div>
              )}

              {/* History */}
              {prevInteractions.length > 0 && (
                <div className="mt-5">
                  <div className="text-xs font-semibold text-slate-600 mb-2">History</div>
                  <div className="space-y-2">
                    {prevInteractions.map(p => (
                      <div key={p.id} className="p-3 border rounded-xl bg-white shadow-sm">
                        <div className="text-[12px] text-slate-500">{p.date} • {p.status}</div>
                        <div className="mt-1 text-[13px] font-semibold text-slate-900">{p.subject}</div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </aside>

          {/* Middle: AI Pipeline */}
          <section className="col-span-6 overflow-y-auto">
            <div className="h-full flex flex-col">
              {/* Steps */}
              <div className="p-4 border-b border-slate-200 bg-white">
                <div className="flex items-center gap-2">
                  <StepPill idx={1} label="Draft" active={step==='draft'} done={step==='cta'} onClick={()=>setStep('draft')} />
                  <StepPill idx={2} label="CTA" active={step==='cta'} onClick={()=>setStep('cta')} />
                </div>
              </div>

              {/* Draft stage */}
              {step === 'draft' && (
                <div className="p-5 space-y-4">
                  <div className="flex items-center gap-2">
                    <label className="text-xs font-medium text-slate-600">Tone</label>
                    <select aria-label="Tone" value={tone} onChange={(e)=>setTone(e.target.value as Tone)} className="px-3 py-1.5 rounded-lg border text-xs">
                      <option>Neutral</option>
                      <option>Friendly</option>
                      <option>Concise</option>
                      <option>Empathetic</option>
                    </select>
                    <button onClick={doGenerate} className="px-3 py-1.5 rounded-lg bg-[#A43131] text-white text-xs font-semibold flex items-center gap-1 disabled:opacity-50" disabled={isGenLoading}>
                      {isGenLoading ? <Loader2 className="w-4 h-4 animate-spin" /> : <Sparkles className="w-4 h-4" />} {suggestions.length ? 'Regenerate' : 'Generate'}
                    </button>
                    <div className="ml-auto" />
                  </div>
                  {/* Chat-style toolbar */}
                  <div className="flex items-center gap-2 text-slate-600">
                    <button onClick={triggerFile} className="px-2 py-1.5 rounded-lg border bg-white text-xs inline-flex items-center gap-1"><Paperclip className="w-4 h-4"/>Attach</button>
                    <input ref={fileInputRef} type="file" multiple onChange={onFilesSelected} className="hidden" aria-label="Attach files" />
                    <button className="px-2 py-1.5 rounded-lg border bg-white text-xs inline-flex items-center gap-1"><ImageIcon className="w-4 h-4"/>Image</button>
                    <button className="px-2 py-1.5 rounded-lg border bg-white text-xs inline-flex items-center gap-1"><Smile className="w-4 h-4"/>Emoji</button>
                  </div>
                  <div>
                    <textarea
                      rows={10}
                      value={draft}
                      onChange={(e)=>onChangeDraft(e.target.value)}
                      placeholder="Write or insert an AI draft reply…"
                      className="w-full rounded-xl border border-slate-300 focus:ring-2 focus:ring-[#A43131]/40 focus:border-[#A43131] p-3 text-sm"
                    />
                  </div>
                  {attachments.length > 0 && (
                    <div className="flex flex-wrap gap-2">
                      {attachments.map((f, i) => (
                        <span key={i} className="px-2 py-1 rounded-lg border bg-white text-[12px] text-slate-700 flex items-center gap-2">
                          {f.name} <span className="text-slate-400">({Math.ceil(f.size/1024)} KB)</span>
                          <button onClick={()=>removeAttachment(i)} className="text-slate-400 hover:text-slate-700">×</button>
                        </span>
                      ))}
                    </div>
                  )}
                  <div className="flex items-center justify-end">
                    <button onClick={()=>setStep('cta')} className="px-4 py-2 rounded-xl text-white font-semibold shadow bg-[#464C47] hover:bg-black">Next: CTA</button>
                  </div>
                  <div>
                    <div className="text-xs font-semibold text-slate-600 mb-2">AI Suggestions</div>
                    <div className="grid sm:grid-cols-2 gap-3">
                      {(suggestions.length ? suggestions : contextualSuggestions).slice(0,4).map((s,i)=> (
                        <div key={i} className="p-3 border rounded-xl bg-white shadow-sm">
                          <div className="text-sm text-slate-800 whitespace-pre-wrap">{s}</div>
                          <div className="mt-2 flex items-center gap-2">
                            <button onClick={()=>onChangeDraft(s)} className="px-2.5 py-1.5 rounded-lg text-xs bg-[#A43131] text-white">Insert</button>
                            <button onClick={()=>navigator.clipboard?.writeText(s)} className="px-2.5 py-1.5 rounded-lg text-xs border bg-white">Copy</button>
                          </div>
                        </div>
                      ))}
                    </div>
                    {!suggestions.length && (
                      <div className="mt-2 text-xs text-slate-500">Contextual suggestions are shown. Select a tone and click Generate to regenerate.</div>
                    )}
                  </div>
                </div>
              )}

              {/* Validate stage removed */}

              {/* CTA stage */}
              {step === 'cta' && (
                <div className="p-5 space-y-4">
                  <div className="flex items-center gap-2">
                    <button onClick={()=>setStep('draft')} className="px-3 py-1.5 rounded-lg border bg-white text-xs">Back</button>
                    <div className="ml-auto" />
                  </div>
                  <div className="grid md:grid-cols-2 gap-3">
                    {ctas.map((c) => (
                      <button
                        key={c}
                        onClick={() => setSelectedCTA(c)}
                        className={`p-3 border rounded-xl bg-white text-left hover:bg-slate-50 flex items-start gap-2 ${selectedCTA === c ? 'border-[#A43131] ring-2 ring-[#A43131]/20' : ''}`}
                      >
                        <Check className={`w-4 h-4 mt-0.5 ${selectedCTA === c ? 'text-[#A43131]' : 'text-slate-300'}`} />
                        <div>
                          <div className="font-semibold text-slate-900 text-sm">{c}</div>
                          <div className="text-[12px] text-slate-600">Will append a clear call-to-action in the closing paragraph.</div>
                        </div>
                      </button>
                    ))}
                  </div>
                  <div>
                    <div className="text-xs font-semibold text-slate-600 mb-2">Optional note (internal)</div>
                    <textarea rows={3} value={note} onChange={(e)=>setNote(e.target.value)} className="w-full rounded-xl border p-2 text-sm" placeholder="Add an internal note for CRM or follow-up" />
                  </div>
                  {/* Send unified in header */}
                </div>
              )}
            </div>
          </section>

          {/* Right: Profile & Recommendations */}
          <aside className="col-span-3 border-l border-slate-200 bg-white overflow-y-auto">
            <div className="p-4 space-y-4">
              <div className="text-xs font-semibold text-slate-600">Customer Profile</div>
              <div className="border rounded-xl p-4 bg-white">
                <div className="flex items-center gap-3">
                  <div className="w-10 h-10 rounded-full bg-[#C29049]/15 text-[#402306] flex items-center justify-center font-bold">
                    {email.from?.[0]?.toUpperCase() || 'C'}
                  </div>
                  <div className="min-w-0">
                    <div className="font-semibold text-slate-900 truncate">{email.from}</div>
                    <div className="text-[12px] text-slate-600 truncate">Contact: {email.from}</div>
                  </div>
                  {(email.segment === 'VIP' || email.category === 'VIP') && (
                    <span className="ml-auto px-2 py-0.5 rounded-full text-[10px] bg-[#C29049]/15 text-[#402306] border border-[#C29049]/40 font-bold">VIP</span>
                  )}
                </div>
                <div className="mt-3 grid grid-cols-2 gap-2 text-[12px] text-slate-700">
                  <div>Segment: <span className="font-semibold">{email.segment || '—'}</span></div>
                  <div>Since: <span className="font-semibold">{email.customerSince || '—'}</span></div>
                  <div>LTV: <span className="font-semibold">{formatMoney(email.lifetimeValue)}</span></div>
                  <div>Current car: <span className="font-semibold">{email.currentCar || '—'}</span></div>
                  <div>Language: <span className="font-semibold">{email.language || '—'}</span></div>
                  <div>SLA: <span className="font-semibold">{email.slaTier || '—'}</span></div>
                </div>
              </div>

              <div className="text-xs font-semibold text-slate-600">AI Confidence</div>
              <div className="border rounded-xl p-3 bg-white flex items-center gap-3">
                <div className={`w-3 h-3 rounded-full ${email.aiConfidence && email.aiConfidence >= 0.9 ? 'bg-emerald-500' : email.aiConfidence && email.aiConfidence >= 0.8 ? 'bg-blue-500' : 'bg-amber-500'}`} />
                <div className="font-bold text-slate-900">{email.aiConfidence ? Math.round(email.aiConfidence*100) : '—'}%</div>
                <div className="text-[12px] text-slate-600">confidence</div>
              </div>

              {email.nextBestAction && (
                <div className="border rounded-xl p-3 bg-white">
                  <div className="text-xs font-semibold text-slate-600 mb-1">AI Recommendation</div>
                  <div className="text-[13px] text-[#464C47]">{email.nextBestAction}</div>
                </div>
              )}

              {/* Quick CTAs on the right rail (contextual) */}
              <div className="text-xs font-semibold text-slate-600">Quick CTA</div>
              <div className="grid grid-cols-2 gap-2">
                {ctas.slice(0,4).map((c) => (
                  <button key={c} onClick={() => { setSelectedCTA(c); setStep('cta'); }} className="px-3 py-2 border rounded-lg bg-white text-left text-[12px] hover:bg-slate-50">
                    {c.toLowerCase().includes('drive') ? <Calendar className="w-4 h-4 inline mr-1"/> : c.toLowerCase().includes('offer') || c.toLowerCase().includes('warranty') || c.toLowerCase().includes('status') ? <FileText className="w-4 h-4 inline mr-1"/> : <PhoneCall className="w-4 h-4 inline mr-1"/>}
                    {c.replace('Schedule ', '').replace('Send ', '')}
                  </button>
                ))}
              </div>
            </div>
          </aside>
        </div>
        {/* Toast */}
        {toast && (
          <div className="absolute top-4 right-4">
            <div className="px-4 py-2 rounded-lg bg-slate-900 text-white text-sm shadow">{toast}</div>
          </div>
        )}
      </div>
    </div>
  );
};

export default EmailResponseWorkspace;
